<template>
  <div id="app">
    <div class="granul-header">
      <Appheader></Appheader>
    </div>
    <router-view/>
    <AppFooter></AppFooter>
  </div>
</template>
<script>
  import Http from '@/axios/http'
  import Api from '@/axios/api'
  import Appheader from '@/components/app-header.vue'
  import AppFooter from '@/components/app-footer.vue'

  export default {
    name: 'home',
    components: {
      Appheader,
      AppFooter
    },
    mounted() {
      let currentToken = localStorage.getItem('token') || '';
      let parma = {
        userToken: currentToken
      }
      if (currentToken) {
        Http.post(parma, Api.userInfo).then(res => {
          let result = res.data
          if (result && Number(result.status) === 200) {
            this.$store.dispatch('addUserinfo', {
              userInfo: result.data
            });
          } else {
            this.$message.error(result.msg);
          }
        })
      }
    }
  }
</script>
<style lang="less">
  @import './assets/css/index.less';

  #app {
    width: 100%;
    height: 100%;
    min-height: 100%;
    display: block;
    overflow: hidden;
    font-family: "Microsoft Yahei", "Hiragino Sans GB", "Helvetica Neue", Helvetica, tahoma, arial, Verdana, sans-serif, "WenQuanYi Micro Hei", "\5B8B\4F53";
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #333;
    background: #f7f7f7;
    font-size: 14px;
    position: relative;
    padding-top: 80px;
    box-sizing: border-box;
    display: block;
    overflow-y: auto;
    /*overflow: hidden;*/
    .granul-header {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 80px;
      display: block;
    }
  }
</style>



