import Vue from 'vue'
import Router from 'vue-router'
import List from '@/views/list.vue'
import Index from '@/views/index.vue'
import Shortvideo from '@/views/shortVideo.vue'
import Commercialvideo from '@/views/commercialVideo.vue'
import Personal from '@/views/personal'
import Order from '@/views/personal/order.vue'
import Income from '@/views/personal/income.vue'
import Integral from '@/views/personal/integral.vue'
import Works from '@/views/personal/works.vue'
import Pay from '@/views/personal/pay.vue'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    }, {
      path: '/list',
      name: 'list',
      component: List
    }, {
      path: '/shortVideo',
      name: 'Shortvideo',
      component: Shortvideo
    }, {
      path: '/commercialVideo',
      name: 'Commercialvideo',
      component: Commercialvideo
    }, {
      path: '/',
      name: 'personal',
      component: Personal,
      children: [
        {
          path: '/personal',
          name: 'order',
          component: Order
        }, {
          path: '/income',
          name: 'income',
          component: Income
        }, {
          path: '/integral',
          name: 'integral',
          component: Integral
        },
        {
          path: '/works',
          name: 'works',
          component: Works
        }, {
          path: '/pay',
          name: 'pay',
          component: Pay
        }
      ]
    }
  ]
})



